.App {
  text-align: center;
  color: white;
  background-image: url(../images/fondonegro.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.eachCharacter {
  list-style: none;
  background-color: whitesmoke;
  width: fit-content;
  color: black;
  display: inline-block;
  border-radius: 15px;
  margin-left: 25px;
  margin-bottom: 25px;
}
.photo {
  width: 100px;
  border-radius: 15px;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
}
.character_name {
  display: flex;
  flex-direction: column;
}
.listCharacter {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  justify-items: center;
}
.noSee {
  display: none;
}
